.caption {
    font-style: italic;
    grid-row-start: 2;
    grid-row-end: 3;

    &.register {
        grid-column-start: 1;
        grid-column-end: 2;
    }

    &.volunteer {
        grid-column-start: 2;
        grid-column-end: 3;
    }
}

.description {
    grid-row-start: 3;
    grid-row-end: 4;

    &.register {
        grid-column-start: 1;
        grid-column-end: 2;
    }

    &.volunteer {
        grid-column-start: 2;
        grid-column-end: 3;
    }
}

.ctaWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 12px;
}

.mainLink {
    display: block;
    flex-grow: 1;
    padding: 20px;
    text-decoration: none;
    background-color: #0075bf;
    border-radius: 4px;
    box-shadow: 1px 1px 3px #a0a0a0;
    grid-row-start: 1;
    grid-row-end: 2;
    text-align: center;

    &.register {
        grid-column-start: 1;
        grid-column-end: 2;
    }

    &.volunteer {
        grid-column-start: 2;
        grid-column-end: 3;
    }
}

.leadText {
    text-align: center;
}

.linkTitle {
    color: white;
    font-size: 36px;
    font-weight: bold;
}

.linkSubtitle {
    color: #f37a31;
    font-weight: bold;
}
